* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-weight: 400;
}

body {
  font-size: 1.6rem;
  font-family: "Lato", sans-serif;
  color: #333;
}

.container {
  width: 90vw;
  margin: 0 auto;
  padding: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
}

.tile {
  border: 1px solid #ccc;
  border-radius: 1rem;
  padding: 2rem 3rem;
  cursor: pointer;
  user-select: none;
  text-align: center;
  width: 100%;
}

.tile img {
  width: 100%;
}

.tile p {
  font-weight: 100;
  line-height: 2;
}

.tile:hover {
  opacity: 0.7;
  transition: all 0.3s;
  transform: translateY(-3px);
}

.spinner-container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.spinner {
  width: 4rem;
  height: 4rem;
  border: 2px solid rgba(128, 128, 128, 0.459);
  border-radius: 50%;
  border-right: 2px solid rgba(0, 0, 0, 0.61);
  animation: spin linear infinite 0.6s;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}